import axios from './axios'
async function getList (id) {
  const list = await find(id)
  return list
}
function find (id) {
  return new Promise((resolve, reject) => {
    axios.get('/dict?groupId=' + id).then((res) => {
      resolve(res)
    }).catch((err) => {
      reject(err)
    })
  })
}

export default getList
